	var mapHTTP = "https://apis.map.qq.com";
	var mapKey = "6ZCBZ-5ZIKF-VAWJ4-NKQI2-6PN6J-UXF4O"
	var dataHTTP = "https://llyapi.laiyunyou.cn/NKOrderManage/NK_Station/GetNKInfo"
	const util = require('../../utils/util.js');
	export default {
		data() {
			return {
				city: "定位中",
				longitude: 102.6621401310,
				latitude: 25.0585733749,
				markers: [], //点
				polyline: [], //线
				inpoints: [], //地图飞的范围
				List: [],

			}
		},
		methods: {
			/**
			 * 通过经纬度获取城市名
			 */
			loadcity() {
				var that = this;
				uni.getLocation({
					success: function(res) {
						console.log(res)
						var longitude = res.longitude
						var latitude = res.latitude
						uni.request({
							url: mapHTTP + '/ws/geocoder/v1/?location=' + latitude + ',' + longitude + '&get_poi=0&key=' + mapKey,
							data: {},
							header: {
								'Content-Type': 'application/json'
							},
							success: function(res) {
								console.log(res)
								that.city = res.data.result.address_component.city
								// success  
							},
							fail: function() {
								page.setData({
									currentCity: "获取定位失败"
								});
							},
						})


					},
					fail: function(res) {
						console.log("未获得地理位置信息")
						console.log(res)
					}

				})



			},

			/**
			 * 绘线
			 */
			drawLine() {

				uni.showLoading({
					title: '加载中',
				})
				var that = this;
				uni.request({
					url: dataHTTP,
					data: {
						beginStationId: 820,
						endStationId: 152
					},
					header: {
						'Content-Type': 'application/json'
					},
					success: function(res) {
						// success  

						uni.hideLoading();

						console.log(res)


						var points = []

						var arrA = res.data.resultdata.NK_RouteInfo.DrawingPoint;
						var arrB = arrA.split(";");


						for (var i = 0; i < arrB.length; i++) {
							var arrC = arrB[i];
							var arrD = arrC.split(",");
							var obj = {
								longitude: arrD[0],
								latitude: arrD[1],
							}
							points.push(obj)
						}

						console.log(points)
						var polyline = [{
							points: points,
							color: "#2894f8",
							width: 4,
						}]


						var mapC = uni.createMapContext('myMap')
						mapC.includePoints({
							padding: [80, 0, 200, 0],
							points: [{
									longitude: points[0].longitude,
									latitude: points[0].latitude
								},
								{
									longitude: points[points.length - 1].longitude,
									latitude: points[points.length - 1].latitude
								}
							]
						})


						//赋值
						that.List = res.data.resultdata.NKCarList;
						that.polyline = polyline;
						that.inpoints = [{
								longitude: points[0].longitude,
								latitude: points[0].latitude
							},
							{
								longitude: points[points.length - 1].longitude,
								latitude: points[points.length - 1].latitude
							}
						];






						that.markers = [{
								iconPath: "/static/images/map_start.png",
								id: 0,
								latitude: points[0].latitude,
								longitude: points[0].longitude,
								width: 12,
								height: 12,
								callout: {
									content: res.data.resultdata.NK_RouteInfo.BeginStationName, //文本
									bgColor: "#ffffff", //背景色
									borderColor: "#cccccc", //边框色
									borderWidth: 1, //边框宽度
									textAlign: "center", //文本对齐方式
									padding: 3, //文本边缘留白
									borderRadius: 1, //边框圆角
									display: "ALWAYS",
								}
							},
							{
								iconPath: "/static/images/map_end.png",
								id: 1,
								latitude: points[points.length - 1].latitude,
								longitude: points[points.length - 1].longitude,
								width: 12,
								height: 12,
								callout: {
									content: res.data.resultdata.NK_RouteInfo.EndStationName, //文本
									bgColor: "#ffffff", //背景色
									borderColor: "#cccccc", //边框色
									borderWidth: 1, //边框宽度
									textAlign: "center", //文本对齐方式
									padding: 3, //文本边缘留白
									borderRadius: 1, //边框圆角
									display: "ALWAYS",

								}
							}
						];



					},
					fail: function() {
						page.setData({
							currentCity: "获取定位失败"
						});
					},
				})

			},

			/**
			 * 点地图上的路线
			 */
			 line:function(){
				uni.createMapContext('myMap').includePoints({
			  padding: [80, 0, 200, 0],
			  points: this.inpoints
			})
			},
				/**
			 * 点地图自己的定位
			 */
			  moveToLocation: function() {
    uni.createMapContext('myMap').moveToLocation()
  },
		/**
	* 点地图自己的定位
	*/
	sweepCode: function(e) {
		 util.sweepCode();
  },

		},
		onLoad() {

			this.loadcity();
			this.drawLine();
		}

	}
